import tkiQrcode from '@/components/tki-qrcode/tki-qrcode.vue'
    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default { 
		data() {
			return {
				datalist:[],
				loadingText: '正在加载...',
				hasmore:true,
				curpage:1,
				maskState: 0, //面板显示状态
				state:20,
				//qrtxt:'123456',
				
				ifShow: true,
				val: 'POINTS:', // 要生成的二维码值
				size: 400, // 二维码大小
				unit: 'upx', // 单位
				//background: '#b4e9e2', // 背景色
				//foreground: '#309286', // 前景色
				//pdground: '#32dbc6', // 角标色
				icon: '', // 二维码图标
				iconsize: 40, // 二维码图标大小
				lv: 3, // 二维码容错级别 ， 一般不用设置，默认就行
				onval: true, // val值变化时自动重新生成二维码
				loadMake: false, // 组件加载完成后自动生成二维码
				src: '' // 二维码生成后的图片地址或base64
				
			}				
		},
        computed: mapState(['hasLogin','userInfo']), 
		components: {tkiQrcode}, 
		onLoad() {
			console.log('页面加载');
			this.loadData();
		},
         onReachBottom() {
             console.log('正在加载中...');
			 this.curpage++;
			 this.loadData();
         },
         onPageScroll(){
             console.log('页面滚动...');
         },
         onPullDownRefresh() {
             console.log('上拉刷新...');
             uni.stopPullDownRefresh();
         },
        methods: {   
            async loadData() {  
				let _this= this;
				console.log('===userInfo==='+this.hasLogin);
				console.log(this.userInfo);
				console.log(this.userInfo.token);
              
				if(!this.hasmore){
					this.loadingText = '暂无更多信息';
					return false;
				}
				if (!this.hasLogin) {
					url="/pages/member/login";
				}else{
					this.$Request.post('member_pointorder/orderlist',{curpage:_this.curpage,state:_this.state}).then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.hasmore =res.hasmore;
							if(!_this.hasmore){
								_this.loadingText = '暂无更多信息';
							}
							console.log(_this.hasmore);
							let _datalist = res.datas.list;
							for(var i=0;i<_datalist.length;i++){
								_this.datalist.push(_datalist[i]);
							}
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'login',
							});
						}
					});
				}
            },
			toPage: function(url) {
				if (!this.hasLogin) {
					url="/pages/member/login";
				} 
				uni.navigateTo({
					url:url,
				})
				
			},
			clickBar: function(state) {
				if (this.state!=state) {
					this.state=state;
					this.hasmore=true;
					this.curpage=1;
					this.datalist=[];
					this.loadingText='正在加载...';
					this.loadData();
				}
				
			},
			//显示面板
			toggleMask(type,info){
				console.log('---type----'+type);
				//this.$refs.qrcode._clearCode();
				this.val = 'POINTS:'+info;
				this.$refs.qrcode._makeCode();
				let timer = type === 'show' ? 10 : 300;
				let	state = type === 'show' ? 1 : 0;
				this.maskState = 2;
				setTimeout(()=>{
					this.maskState = state;
				}, timer)
			},
			stopPrevent(e){
				console.log('======stopPrevent=====');
				console.log(e);
			},
			qrR(res) {
				this.src = res
			},
			toDel(orderid){
				var _this= this;
				
				uni.showModal({
					title: '提示',
					content: '确认要删除吗？',
					success: function (re) {
						if (re.confirm) {
							 uni.showLoading({title: '正在处理中...'});
							_this.$Request.post('member_pointorder/orderdel',{orderid:orderid}).then(res => {
								if (res.code == 200) {
									console.log(res);
									let len = _this.datalist.length;
									for(let i=0;i<len;i++){
										if(orderid==_this.datalist[i].point_orderid){
											_this.datalist.splice(i, 1);
											break;
										}
									}
									uni.hideLoading();
								}
								else{
									uni.hideLoading();
									uni.showToast({
										icon: 'none',
										title:res.datas.error,
										duration:2000
									});
								}
							});
						}
					},
				})
				
			}
			 
        }  
    }  
